.pepicons-popclock-off {
  width: 2.333rem;
  position: relative;
  height: 2.333rem;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.pepicons-popclock-off-wrapper {
  border-radius: 100px;
  background: linear-gradient(rgba(52, 74, 81, 0.26), rgba(52, 74, 81, 0.26)),
    #fff;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0.833rem;
}
.content-protection {
  margin: 0;
}
.stay-focused-on {
  margin: 0;
  font-weight: 300;
}
.content-protection-stay-container {
  width: 20rem;
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
  display: inline-block;
}
.frame-parent6 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 1.333rem;
  gap: 2rem;
  text-align: center;
  font-size: 1.333rem;
  color: #242323;
  font-family: Inter;
}
