.quell {
  margin-block-start: 0;
  margin-block-end: 20px;
}
.the-spell {
  margin: 0;
}
.quell-the-spell-container {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 8.583rem;
  font-weight: 600;
  font-family: inherit;
}
.reclaim-your-life {
  align-self: stretch;
  position: relative;
  font-size: 3rem;
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
  font-family: Inter;
  color: #242323;
}
.hero-text1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 5rem;
}
.icbaseline-apple-icon1 {
  width: 2rem;
  position: relative;
  height: 2rem;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.app-store {
  position: relative;
}
.button3 {
  flex: 1;
  border-radius: 100px;
  background-color: #303034;
  height: 4.667rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 1.333rem 2.666rem;
  box-sizing: border-box;
  gap: 0.833rem;
}
.image-14-icon1 {
  width: 2rem;
  position: relative;
  height: 2rem;
  object-fit: cover;
}
.play-store {
  position: relative;
  font-size: 1.667rem;
  font-family: Inter;
  color: #fff;
  text-align: center;
}
.button4 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  flex: 1;
}
.download-now-button1 {
  width: 41.667rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 1.333rem;
  font-size: 1.667rem;
  color: #fff;
  font-family: Inter;
}
.hero-text-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 3.666rem;
}
.iphone-12-pro-61-mockup-1 {
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 33.333rem;
  object-fit: cover;
}
.iphone-12-pro-61-mockup-1-parent {
  width: 95.333rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: 2.666rem;
}
.hero-inner,
.hero1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.hero1 {
  background-color: #fff;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 7.333rem 0 0;
  gap: 8rem;
  text-align: center;
  font-size: 10rem;
  color: #0b1116;
  font-family: Poppins;
}
@media screen and (max-width: 960px) {
  .hero-inner {
    flex-direction: column;
    gap: 5.333rem;
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 420px) {
  .button3,
  .button4 {
    flex: unset;
    align-self: stretch;
  }
  .download-now-button1,
  .hero-inner {
    flex-direction: column;
    gap: 0.667rem;
    align-items: flex-start;
    justify-content: center;
  }
  .hero-inner {
    gap: -19.167rem;
    align-items: center;
  }
}
