.the-quell-app {
  font-weight: 300;
}
.the-quell-app-container {
  width: 73.5rem;
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
  display: inline-block;
}
.frame-parent10,
.frame-parent8,
.frame-parent9,
.video {
  align-self: stretch;
  display: flex;
  align-items: center;
  gap: 5.333rem;
}
.frame-parent10,
.frame-parent9 {
  flex-direction: row;
  justify-content: center;
  padding: 0 5.333rem;
}
.frame-parent8,
.video {
  flex-direction: column;
  justify-content: flex-start;
  font-size: 1.333rem;
}
.video {
  background-color: #fff;
  padding: 7.333rem 0;
  gap: 8rem;
  text-align: center;
  font-size: 2rem;
  color: #242323;
  font-family: Inter;
}
@media screen and (max-width: 420px) {
  .frame-parent10,
  .frame-parent9 {
    flex-direction: column;
    gap: 1.333rem;
    align-items: center;
    justify-content: center;
  }
}
