.vector-icon3,
.vector-icon4 {
  width: 0.592rem;
  position: relative;
  height: 0.55rem;
  object-fit: cover;
}
.vector-icon4 {
  width: 1.167rem;
  height: 0.5rem;
}
.endless-scrolling {
  width: 1.333rem;
  height: 2.333rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 0.366rem;
}
.endless-scrolling-wrapper {
  width: 4rem;
  border-radius: 100px;
  background: linear-gradient(rgba(52, 74, 81, 0.26), rgba(52, 74, 81, 0.26)),
    #fff;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0.833rem;
  box-sizing: border-box;
}
.personalized-support {
  margin: 0;
}
.get-real-time-friendly {
  margin: 0;
  font-weight: 300;
}
.personalized-support-get-container {
  width: 20rem;
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
  display: inline-block;
}
.frame-parent7 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 1.333rem;
  gap: 2rem;
  text-align: center;
  font-size: 1.333rem;
  color: #242323;
  font-family: Inter;
}
