.get-early-access {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 6.5rem;
  font-weight: 600;
  font-family: inherit;
}
.get-early-access-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.icbaseline-apple-icon2 {
  width: 2rem;
  position: relative;
  height: 2rem;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.app-store1 {
  position: relative;
}
.button5 {
  border-radius: 100px;
  background-color: #303034;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 1.333rem 2.666rem;
  gap: 0.833rem;
}
.image-14-icon2 {
  width: 2rem;
  position: relative;
  height: 2rem;
  object-fit: cover;
}
.button-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 2rem;
}
.download-now-button2,
.frame-parent11 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.download-now-button2 {
  align-items: center;
  font-size: 1.667rem;
  color: #fff;
  font-family: Inter;
}
.frame-parent11 {
  align-items: flex-start;
  gap: 2.666rem;
}
.enter-your-email1,
.submit1 {
  position: relative;
  display: inline-block;
}
.enter-your-email1 {
  width: 56.167rem;
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
}
.submit1 {
  width: 7rem;
  font-size: 2rem;
  font-family: Inter;
  color: #f1f1f1;
  text-align: center;
  flex-shrink: 0;
}
.button7 {
  cursor: pointer;
  border: 0;
  padding: 0.833rem 2.666rem;
  background-color: #303034;
  align-self: stretch;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.email-field1,
.enter-your-email-below-to-beco-parent {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.email-field1 {
  border-radius: 100px;
  background-color: #fff;
  border: 1px solid #303034;
  box-sizing: border-box;
  height: 5.333rem;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: row;
  padding: 0 0 0 2.333rem;
  gap: 5.333rem;
  text-align: left;
  color: #303034;
}
.enter-your-email-below-to-beco-parent {
  align-self: stretch;
  flex-direction: column;
  gap: 2.666rem;
  font-size: 1.667rem;
  color: #242323;
  font-family: Inter;
}
.vector-icon6 {
  width: 8.2rem;
  position: relative;
  height: 9.725rem;
  object-fit: cover;
}
.vector-icon7,
.vector-icon8 {
  width: 5.817rem;
  position: relative;
  height: 10rem;
  object-fit: contain;
}
.vector-icon8 {
  width: 8.2rem;
  height: 9.725rem;
}
.cta,
.vector-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.cta {
  align-self: stretch;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7.333rem 0;
  gap: 5.333rem;
  text-align: center;
  font-size: 6rem;
  color: #0b1116;
  font-family: Poppins;
}
@media screen and (max-width: 420px) {
  .email-field1 {
    flex-direction: column;
    gap: 0.833rem;
    align-items: center;
    justify-content: flex-start;
  }
}
