.objects-icon {
  width: 11.033rem;
  position: relative;
  height: 4.667rem;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.features {
  flex: 1;
  position: relative;
}
.items1 {
  margin: 0;
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-end;
  gap: 2.666rem;
  text-align: center;
  font-size: 1.667rem;
  color: #000;
  font-family: Inter;
}
.image-14-icon {
  width: 2rem;
  height: 2rem;
  object-fit: cover;
}
.get,
.image-14-icon {
  position: relative;
}
.button1 {
  flex: 1;
  border-radius: 100px;
  background-color: #303034;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
  gap: 0.833rem;
}
.icbaseline-apple-icon {
  width: 2rem;
  position: relative;
  height: 2rem;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.button-parent,
.navbar1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.button-parent {
  width: 20.833rem;
  flex-wrap: wrap;
  gap: 1.333rem;
}
.navbar1 {
  align-self: stretch;
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1.333rem 5.333rem;
  gap: 25rem;
  text-align: center;
  font-size: 1.667rem;
  color: #fff;
  font-family: Inter;
}
@media screen and (max-width: 1200px) {
  .navbar1 {
    flex-direction: row;
    gap: 25rem;
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 960px) {
  .navbar1 {
    flex-direction: row;
    gap: 5.333rem;
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 420px) {
  .features {
    flex: unset;
    align-self: stretch;
  }
  .items1,
  .navbar1 {
    flex-direction: column;
  }
  .items1 {
    gap: 1.333rem;
    align-items: flex-start;
    justify-content: flex-end;
    flex: unset;
    align-self: stretch;
  }
  .navbar1 {
    gap: 0.333rem;
    align-items: center;
    justify-content: flex-start;
  }
}
