.contact-us-group {
  width: 41.667rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 2.666rem;
}
.quell-app- {
  flex: 1;
  position: relative;
}
.quell-app-your-ai-powered-di-wrapper {
  width: 33.333rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  font-size: 1rem;
}
.footer1 {
  width: 120rem;
  background: linear-gradient(#fff, #fff), #242323;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7.333rem 0;
  gap: 2.666rem;
  text-align: center;
  font-size: 1.667rem;
  color: #303034;
  font-family: Inter;
}
@media screen and (max-width: 960px) {
  .footer1 {
    gap: 2.667rem;
    padding: 3.667rem 2.667rem;
    box-sizing: border-box;
    opacity: 0;
  }
  .footer1.animate {
    animation: 1s ease 0s 1 normal forwards slide-in-top;
  }
  @keyframes slide-in-top {
    0% {
      transform: translateY(-200px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
@media screen and (max-width: 420px) {
  .contact-us-group,
  .quell-app-your-ai-powered-di-wrapper {
    flex-direction: row;
    gap: 0.333rem;
    align-items: flex-start;
    justify-content: center;
  }
  .footer1 {
    gap: 1.333rem;
    padding: 1rem 1.333rem;
    box-sizing: border-box;
    opacity: 0;
  }
  .footer1.animate {
    animation: 1s ease 0s 1 normal forwards slide-in-top;
  }
  @keyframes slide-in-top {
    0% {
      transform: translateY(-200px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
